import React, { FC, useCallback, useState, useRef } from 'react';
import { Link } from 'umi';
import style from '../index.less';
import ChunkTitle from '@/components/chunkTitle';
import CommonItemWrap from '@/components/CommonItemWrap';
import { CommontItem } from '@/types/types';
import { RightOutlined } from '@ant-design/icons';
import { Spin, Empty } from 'antd';
 
interface Props{
  title: string;
  enterUrl: string;
  commonList: CommontItem[];
  loading: boolean;
  refresh?: () => void;
  CommontCount?: any;
  showBlackBtn?: boolean;
}

const CommonChunk = (props: Props) => {
  const { title, enterUrl, commonList, loading, refresh, CommontCount, showBlackBtn } = props;

  return (
    <div className={ style.productionManageChunk }>
      <Spin tip="加载中..." spinning={ loading }>
        <ChunkTitle title={ title } enterUrl={ enterUrl } />
        <div>
          {commonList?.length
            ? commonList?.map((item: CommontItem, index: number) => (
              <CommonItemWrap showBlackBtn={ showBlackBtn } key={ index } common={ item } CommontCount={ CommontCount } refresh={ refresh } />
            ))
            : <Empty />
          }
        </div>
        <Link to={ enterUrl } className={ style.lookMore }>
          <span>查看更多</span>
          <RightOutlined />
        </Link>
      </Spin>
    </div>
  );
};

export default CommonChunk;
