import { css } from '@emotion/core';
import React, { ReactElement, FC, useMemo } from 'react';
import { Row, Col, Button } from 'antd';
import { ProductionDetailInfo } from '@/types/types';
import { ProductionClass } from '@/utils/constant.js';
import moment from 'moment';
import style from './index.less';

export interface Props {
  production: ProductionDetailInfo;
  btnRender?: any;
  imgSpan?: number;
  detailSpan?: number;
  btnSpan?: number;
}


const ProductionDetail: FC<Props> = (props: Props) => {
  const { production = {}, btnRender, imgSpan, detailSpan, btnSpan } = props;
  const coverImage = useMemo(() => {
    const imgList = production?.coverImage ? JSON.parse(production?.coverImage) : [];

    return imgList?.length ? imgList[0] : '';
  }, [production?.coverImage]);

  return (
    <Row>
      <Col span={ imgSpan ?? 4 }>
        {coverImage ? <img className={ style.productionImg } src={ coverImage } /> : ''}
      </Col>
      <Col span={ ((detailSpan || 0) + (btnSpan || 0)) || 20 } className={ style.productionInfo }>
        <Row>
          <Col span={ detailSpan ? (detailSpan / 20 * 24) : 10 }>
            <h3 style={{ marginBottom: 14 }}>{production?.title}</h3>
            <p>
              <span className={ style.productionInfoText }>品类：{ProductionClass[production?.category]}</span>
              <span>上映时间：{moment(production?.showTime).format('YYYY-MM-DD')}</span>
            </p>
          </Col>
          <Col span={ btnSpan ? (btnSpan / 20 * 24) : 14 } style={{ paddingLeft: 30 }}>
            {btnRender}
          </Col>
        </Row>
        <div>
          <p>简介：</p>
          <p style={{ whiteSpace: 'pre-line' }}>{production?.description}</p>
        </div>
      </Col>
      
      {/* <Col span={ detailSpan ?? 8 } className={ style.productionInfo }>
        <h3>{production?.title}</h3>
        <div>
          <p>
            <span className={ style.productionInfoText }>品类：{ProductionClass[production?.category]}</span>
            <span>上映时间：{moment(production?.showTime).format('YYYY-MM-DD')}</span>
          </p>
          <p>简介：{production?.description}</p>
        </div>
      </Col>
      <Col span={ btnSpan ?? 12 } style={{ paddingLeft: 30 }}>
        {btnRender}
      </Col> */}
    </Row>
  );
};

export default ProductionDetail;
