import React, { FC, useCallback, useState, useRef, useEffect } from 'react';
import { Link } from 'umi';
import style from '../index.less';
import ChunkTitle from '@/components/chunkTitle';
import CommonItemWrap from '@/components/CommonItemWrap';
import { CommontItem } from '@/types/types';
import { RightOutlined } from '@ant-design/icons';
import { InputNumber, Spin } from 'antd';
import { CloseOutlined, CheckOutlined, EditOutlined } from '@ant-design/icons';
 
interface Props{
  score: number;
  onOk: (type: string, num: number) => void;
  type: 'high'| 'low';
}

const ScoreInput = (props: Props) => {
  const { score, onOk, type } = props;
  const [nowScore, setNowScore] = useState(score);
  const [canEdit, setCanEdit] = useState(false);

  useEffect(() => {
    setNowScore(score);
  }, [score]);

  const handleCancle = () => {
    setNowScore(score);
    setCanEdit(false);
  };

  const handleOk = () => {
    onOk(type, nowScore);
    handleCancle();
  };

  return (
    canEdit
      ? (
        
        <InputNumber max={ 10 } precision={ 1 }
          addonBefore={ <CloseOutlined onClick={ handleCancle } /> }
          onChange={ setNowScore }
          addonAfter={ <CheckOutlined onClick={ handleOk } /> }
          value={ nowScore }
          style={{ width: 150 }}
        />
      )
      : (
        <>
          <span>{nowScore}</span>
          <EditOutlined onClick={ () => setCanEdit(true) } />
        </>
      )
  );
};

export default ScoreInput;
