import React, { FC, useCallback, useState, useRef, useMemo } from 'react';
import { Link } from 'umi';
import { Button, Space } from 'antd';
import QueryTable, { OnSearch, Props as QueryTableProps } from '@/components/QueryTable';
import { withAuthorized } from '@/utils/Authorized';
import { Role, getAuthority } from '@/utils/authority';
import ProductionDetail from '@/components/ProductionDetail';
import ChunkTitle from '@/components/chunkTitle';
import CommonChunk from './components/CommonChunk';
import style from './index.less';
import { ProductionOutline } from '@/components/miniComponents';
import { useGetCommontList, useGetProductionDetail } from '@/services/publicServices';
import DataInputControl from '@/components/DataInputControl/index';
import DataTempleteUpload from '@/components/DataTempleteUpload';
import ScoreNumberControl from '@/components/ScoreNumberControl/index';
import RegulationBtns from './components/RegulationBtns';

const PAGESIZE = 2;
const authority = getAuthority();
const authData = {
  system: authority.indexOf(Role.SYSTEM) >= 0,
  works: authority.indexOf(Role.WORKS) >= 0,
  comment: authority.indexOf(Role.COMMENT) >= 0,
  expert: authority.indexOf(Role.EXPERT) >= 0,
};

const ProductionManage = (props: any) => {
  const { match: { params } } = props;
  const { productionDetail, detailLoading, mutate } = useGetProductionDetail(params?.id);

  const expertInitSearch = useMemo(() => ({
    pagination: { pageSize: PAGESIZE, current: 1 },
    query: {
      subjectId: params?.id,
      commentUserRank: 'expert',
      checkStatus: undefined,
      rootId: 0
    }
  }), [params?.id]);
  const normalInitSearch = useMemo(() => ({
    pagination: { pageSize: PAGESIZE, current: 1 },
    query: {
      subjectId: params?.id,
      commentUserRank: 'common',
      checkStatus: undefined,
      rootId: 0
    }
  }), [params?.id]);

  const { commontList: expertCommontList, CommontCount: expertCommontCount, isValidating: expertLoading, mutate: expertRefresh } = useGetCommontList(expertInitSearch);
  const { commontList: normalCommontList, CommontCount: normalCommontCount, isValidating: normalLoading, mutate: normalRefresh } = useGetCommontList(normalInitSearch);

  const handleSuccess = () => {
    mutate();
  };
  
  return (
    <div className={ style.productionManageWrap }>
      {authData?.system || authData?.works ? (
        <div className={ style.productionManageChunk }>
          <ChunkTitle title="作品资料管理" enterUrl={ `/content-manage/production-data?dramaId=${ productionDetail.dramaId || 111 }` } />
          <ProductionDetail production={ productionDetail }
            btnRender={
              <Space size={ 30 }>
                <div>
                  <Link to={ `/content-manage/production-data?dramaId=${ productionDetail.dramaId || 111 }` }>
                    <Button type="primary">修改资料</Button>
                  </Link>
                  <br />
                  <ProductionOutline style={{ marginTop: 10 }} production={ productionDetail } refresh={ mutate } />
                </div>
                <RegulationBtns productionDetail={ productionDetail } />
              </Space>
            }
          />
        </div>) : null}
      {authData?.system || authData?.comment ? (
        <CommonChunk title="大众评论管理" showBlackBtn={ true }
          enterUrl={ `/commont-manage/public-comment-manage?dramaId=${ productionDetail.dramaId }` }
          commonList={ normalCommontList }
          CommontCount={ normalCommontCount }
          loading={ normalLoading }
          refresh={ normalRefresh }
        />) : null}
      {authData?.system || authData?.expert ? (
        <CommonChunk title="专业评论管理" showBlackBtn={ false }
          enterUrl={ `/commont-manage/expert-comment-manage?dramaId=${ productionDetail.dramaId }` }
          CommontCount={ expertCommontCount }
          commonList={ expertCommontList }
          loading={ expertLoading }
          refresh={ expertRefresh }
        />) : null}
      {authData?.system && (
        <>
          <div className={ style.productionManageChunk }>
            <ChunkTitle title="评分人数调控" enterUrl={ `/data-manage/data-control?dramaId=${ productionDetail.dramaId }` } />
            <ScoreNumberControl dramaInfo={ productionDetail } successHandle={ handleSuccess } />
          </div>
          <div className={ style.productionManageChunk }>
            <ChunkTitle title="数据调控" enterUrl={ `/data-manage/data-control?dramaId=${ productionDetail.dramaId }` } />
            <DataInputControl dramaId={ params?.id } successHandle={ handleSuccess } />
          </div>
          <div className={ style.productionManageChunk }>
            <ChunkTitle title="灌评" enterUrl={ `/data-manage/data-control?dramaId=${ productionDetail.dramaId }` } />
            <DataTempleteUpload dramaId={ params?.id } successHandle={ handleSuccess } />
          </div>
        </>)}
      
    </div>
    
  );
};

export default withAuthorized(ProductionManage, [Role.SYSTEM, Role.WORKS, Role.COMMENT, Role.EXPERT]);
