import React, { FC, useCallback, useState, useRef } from 'react';
import { Link } from 'umi';
import style from './index.less';
import { Button } from 'antd';

interface Props{
  title: string;
  enterUrl?: string;
}

const ChunkTitle = (props: Props) => {
  const { title, enterUrl } = props;
  
  return (
    <p className={ style.chunkTitle }>
      <span className={ style.chunkTitleText }>{title}</span>
      {enterUrl
        ? (
          <Link to={ enterUrl }>
            <Button type="primary" size="small">进入</Button>
          </Link>)
        : ''}
    </p>
    
  );
};

export default ChunkTitle;
