import React, { FC, useCallback, useState, useRef } from 'react';
import { Spin, Empty, Button, message, Popconfirm, Space } from 'antd';
import { ProductionDetailInfo } from '@/types/types';
import {
  useProductionBtnAuth, setHightScoreLine, openLowScoreWarm, setLowScoreLine
} from '../services';
import ScoreInput from './ScoreInput';
import ScoreWarm from './ScoreWarm';

 
interface Props{
  productionDetail?: ProductionDetailInfo;
}

const RegulationBtns = (props: Props) => {
  const { productionDetail } = props;
  const { btnAuth, loading, mutate } = useProductionBtnAuth(productionDetail?.dramaId);
  const [inputNumloading, setInputNumloading] = useState({ high: false, low: false });

  const handleSetScoreWarm = useCallback(async (type: string, score: number) => {
    try {
      let service, scoreName;

      switch (type) {
        case 'high': service = setHightScoreLine; scoreName = 'highScore'; break;
        case 'low': service = setLowScoreLine; scoreName = 'lowScore'; break;
      }
      setInputNumloading((pre: any) => ({ ...pre, [type]: true }));
      const res = await service({
        dramaId: productionDetail?.dramaId,
        [scoreName]: score
      });

      if (res?.code === 200) {
        message.success('设置成功！');
        mutate();
      }
    } catch (error) {
      message.error(error?.message);
    } finally {
      setInputNumloading((pre: any) => ({ ...pre, [type]: false }));
    }
  }, [mutate, productionDetail]);

  return (
    <Spin spinning={ loading }>
      <Space size={ 30 }>
        <Spin spinning={ inputNumloading?.high }>
          <ScoreInput score={ btnAuth?.highScore || 0 } type="high" onOk={ handleSetScoreWarm } />
          <br />
          <ScoreWarm dramaId={ productionDetail?.dramaId } refresh={ mutate } type="high" status={ btnAuth?.enabledHighScoreAlarm } setInputNumloading={ setInputNumloading } />
        </Spin>
        <Spin spinning={ inputNumloading?.low }>
          <ScoreInput score={ btnAuth?.lowScore || 0 } type="low" onOk={ handleSetScoreWarm } />
          <br />
          <ScoreWarm dramaId={ productionDetail?.dramaId } refresh={ mutate } type="low" status={ btnAuth?.enabledLowScoreAlarm } setInputNumloading={ setInputNumloading } />
        </Spin>
      </Space>
    </Spin>
  );
};

export default RegulationBtns;
