export interface CloseWarmingParams{
  dramaId: string;
  isEnabledAlarm: 0 | 1;
}
// 是否开启作品报警 /wind-vane-backend/drama/operate/enabled/alarm
export function closeWarming(params: CloseWarmingParams) {
  return request(`${ requestBasePath }/wingvate/wind-vane-backend/drama/operate/enabled/alarm?${ stringify(params) }`, {
    method: 'POST',
    body: params
  });
}

