import React, { FC, useCallback, useState, useRef, useMemo } from 'react';
import { Link } from 'umi';
import style from '../index.less';
import ChunkTitle from '@/components/chunkTitle';
import CommonItemWrap from '@/components/CommonItemWrap';
import { CommontItem } from '@/types/types';
import { RightOutlined } from '@ant-design/icons';
import { Popconfirm, message, Button } from 'antd';
import { CloseOutlined, CheckOutlined } from '@ant-design/icons';
import {
  openHightScoreWarm, openLowScoreWarm,
} from '../services';
 
interface Props{
  dramaId: string;
  refresh: () => void;
  type: 'high'| 'low';
  status: 0 | 1;
  setInputNumloading: (params: any) => void;
}

const ScoreWarm = (props: Props) => {
  const { dramaId, refresh, type, status, setInputNumloading } = props;
  const { revertStatus, revertStatusText } = useMemo(() => {
    if (status === 0) {
      return {
        revertStatus: 1,
        revertStatusText: '开启'
      };
    }

    return {
      revertStatus: 0,
      revertStatusText: '关闭'
    };
  }, [status]);

  const handleCloseWarming = useCallback(async () => {
    let serviceName: any, statusName = '';

    switch (type) {
      case 'high':
        serviceName = openHightScoreWarm; statusName = 'enabledHighScoreAlarm';
        break;
      case 'low':
        serviceName = openLowScoreWarm; statusName = 'enabledLowScoreAlarm';
        break;
    }
    try {
      const params = {
        dramaId,
        [statusName]: revertStatus,
      };

      setInputNumloading((pre: any) => ({ ...pre, [type]: true }));
      const res = await serviceName(params);

      if (res?.code === 200) {
        message.success(`${ revertStatusText }预警成功！`);
        refresh();
      }
    } catch (error) {
      message.error(error?.message || '服务器出问题啦');
    } finally {
      setInputNumloading((pre: any) => ({ ...pre, [type]: false }));
    }
  }, [type, dramaId, revertStatus, setInputNumloading, revertStatusText, refresh]);

  const btnText = useMemo(() => {
    switch (type) {
      case 'high': return '高分';
      case 'low': return '低分';
    }
  }, [type]);

  return (
    <Popconfirm placement="bottomRight"
      title={ `确认${ revertStatusText }${ btnText }预警吗？` }
      onConfirm={ handleCloseWarming }
      okText="确认"
      cancelText="取消"
    >
      <Button style={{ marginTop: 10 }}>{revertStatusText}{btnText}预警</Button>
    </Popconfirm>
  );
};

export default ScoreWarm;
